.circle {
    width: 71px;
    height: 71px;
    line-height: 200px;
    border-radius: 50%; /* the magic */
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    text-align: center;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 700;
    /*margin: 0 0 0 40px;*/
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid rgba(117, 120, 129, 1);
    color: rgba(117, 120, 129, 1);
}

.circle_wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100px;
    margin: 20px 0;

}

.circle_wrapper p {
    margin: 0;
    padding: 0;
    color: rgba(117, 120, 129, 1);
    margin-top: 10px;
}