@media screen and (min-width: 320px) {
    .header {
        height: 60px;
        background: #000;
        color: #fff;
        display: flex;
        align-items: center;
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        z-index: 999;
        -webkit-backface-visibility: hidden;
    }

    .line {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 9999;
        max-width: 1590px;
        width: 100%
    }

    .header__items {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .header_items__item {
        height: 24px;
        width: 21px;
        transition: all 0.4s ease;
        cursor: pointer;
    }

    .header_items__item:hover {
        fill: #ff9e1f;
        color: red;
    }
}