.profile_header {
    height: 175px;
    background-size: cover;
    padding: 0 20px;
    background: no-repeat 0 center;
    position: relative;
}

.profile_header__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
    justify-content: space-between;
}

.profile__img {
    height: 84px;
    width: 84px;
}

.line {
    background: #fff;
    height: 1px;
    width: 80px;
    margin: 14px 0;
    display: flex;
    align-items: flex-end;
    margin-left: 17px;
}

.profile__info {
    width: 100px;
}

.profile_info__item {
    width: 100%;
    display: flex;
    align-items: center;
}

.profile_info__item p {

    color: #9a9ca6;
    margin: 0;
    font-size: 12px;
    font-weight: 500;
    margin-left: 7px;
    margin-top: 3px;
}

.profile__info p:nth-child(1) {
    margin-left: 17px;
}

.profile_info__item img {
    height: 10px;
    width: 10px
}

.profile__info h2 {
    color: #f2702c;
    font-size: 20px;
    margin: 0;
    padding: 0;
    margin-left: 7px;
}

.profile__icons {
    display: flex;
    flex-direction: column;
    height: 100px;
    justify-content: space-between;
    align-items: center;
}

.profile__icons img {
    height: 30px;
}

.profile__icons img:first-child {
    height: 25px;
    width: 25px
}

.line_header {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 999999;
    max-width: 1590px;
    width: 100%
}

